import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPowerOff } from "@fortawesome/free-solid-svg-icons";
import { appOperations } from "app/store/state/app";
import { useDispatch } from "react-redux";
import "./header.scss";

function Header(props) {
  const dispatch = useDispatch();

  const logout = (e) => {
    e.stopPropagation();
    dispatch(appOperations.logout());
  };

  return (
    <div
      id="cfg_header"
      className="align-items-center d-flex justify-content-between pl-4 pr-4"
    >
      <div className="logo-wrapper text-white font-weight-bold">LMS</div>
      <div className="button-wrapper pointer" onClick={logout}>
        <FontAwesomeIcon icon={faPowerOff} />
      </div>
    </div>
  );
}

export default Header;
